.vetskitchen-buttons-with-image {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 30px;
  padding: 30px 0;
}
.vetskitchen-buttons-with-image a {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  padding: 10px;
  width: 100%;
  color: #ffffff;
  background-color: #008077;
  border-radius: 20px;
}
.vetskitchen-buttons-with-image a .text-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 26px;
}
.vetskitchen-buttons-with-image a .text-wrapper svg {
  max-width: 10px;
}
.vetskitchen-buttons-with-image a .text {
  position: relative;
  font-size: 26px;
  font-weight: bold;
}
.vetskitchen-buttons-with-image a .text:after {
  content: "";
  position: absolute;
  bottom: -16px;
  left: -8px;
  width: 144px;
  height: 10px;
  background: url("../../assets/images/border__image.png");
  background-repeat: no-repeat;
}
.vetskitchen-buttons-with-image a > img {
  max-width: 100px;
  height: auto;
}
.vetskitchen-buttons-with-image a:hover {
  text-decoration: none;
}